import { Component } from 'react';
import Button from '../../button';
import notification from '../index';
import Icon from '../../icon';
import message from '../../message';


export default class PopoverDemo extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const config = () => {
      notification.config({
        placement: 'bottomRight',
        bottom: 50,
        duration: 0,
        getContainer: 'App',
      });
      message.success('');
    };

    const openNotification = () => {
      notification.open({
        message: '',
        description: '',
      });
    };

    const openInfo = () => {
      notification.open({
        type: 'info',
        message: '',
        description: '',
      });
    };

    const openSuccess = () => {
      notification.open({
        type: 'success',
        message: '',
        description: '',
      });
    };

    const openError = () => {
      notification.open({
        type: 'error',
        message: '',
        description: '',
      });
    };

    const openWaring = () => {
      notification.open({
        type: 'warning',
        message: '',
        description: '',
      });
    };

    const openCaution = () => {
      notification.open({
        type: 'caution',
        message: '',
        description: '',
      });
    };

    const openNoDuration = () => {
      notification.open({
        message: '',
        description: '',
        duration: 0,
      });
    };

    const openIcon = () => {
      notification.open({
        message: '',
        description: '',
        duration: 0,
        icon: <Icon
          style={{
            top: '16px',
            left: '24px',
            position: 'absolute',
          }}
          name={'clock'}
        />,
      });
    };

    const openButton = () => {
      const key = `open${Date.now()}`;
      const btnClick = () => {
        notification.close(key);
      };
      const btn = (
        <div>
          <Button type="primary" size="small" onClick={btnClick}>
          
          </Button>
        &emsp;
          <Button type="secondary" size="small" onClick={btnClick}>
          
          </Button>
        </div>
      );

      notification.open({
        type: 'warning',
        message: '',
        description: '608-10',
        key,
        btn,
      });
    };


    const openButtonLink = () => {
      const btnlink = (
        <a href="./notification"></a>
      );
      notification.open({
        type: 'warning',
        message: '',
        description: '608-10',
        btn: btnlink,
      });
    };

    const openPlacement = (placement) => {
      notification.open({
        placement,
        message: '',
        description: '',
      });
    };

    return (
      <div className="markdown-block">
        <h3></h3>
        <p>4.5 </p>
        <Button onClick={openNotification}>open</Button>&emsp;
        <h3></h3>
        <p></p>
        <Button onClick={openInfo}>info</Button>&emsp;
        <Button onClick={openSuccess}>success</Button>&emsp;
        <Button onClick={openError}>error</Button>&emsp;
        <Button onClick={openWaring}>waring</Button>&emsp;
        <Button onClick={openCaution}>caution</Button>&emsp;
        <h3></h3>
        <p></p>
        <Button onClick={openIcon}>openIcon</Button>&emsp;
        <h3></h3>
        <p>4.5</p>
        <Button onClick={openNoDuration}>open</Button>&emsp;
        <h3></h3>
        <p></p>
        <Button onClick={openButton}>openButton</Button>&emsp;
        <Button onClick={openButtonLink}>openLink</Button>&emsp;
        <h3></h3>
        <p></p>
        <Button onClick={() => openPlacement('topRight')}>topRight</Button>&emsp;
        <Button onClick={() => openPlacement('topLeft')}>topLeft</Button>&emsp;
        <Button onClick={() => openPlacement('bottomLeft')}>bottomLeft</Button>&emsp;
        <Button onClick={() => openPlacement('bottomRight')}>bottomRight</Button>&emsp;
        <h3></h3>
        <p></p>
        <p>
          {`notification.config({
            placement: 'bottomRight',
            bottom:50,
            duration:0,
            getContainer:'App'
          });`}
        </p>
        <Button onClick={() => config()}>config</Button>&emsp;
        <Button onClick={openNotification}>open</Button>&emsp;
      </div>
    );
  }
}



